@keyframes barTop {
    0%{
        width: 20px;
    }
    25%{
        width: 30px
    }
    50%{
        width: 40px;
    }
    75%{
        width: 30px;
    }
    100%{
        width: 20px;
    }
}

@keyframes barMiddle {
    0%{
        width: 30px;
    }
    25%{
        width: 40px
    }
    50%{
        width: 20px;
    }
    75%{
        width: 40px;
    }
    100%{
        width: 30px;
    }
}

@keyframes barBottom {
    0%{
        width: 40px;
    }
    25%{
        width: 20px
    }
    50%{
        width: 30px;
    }
    75%{
        width: 20px;
    }
    100%{
        width: 40px;
    }
}


@keyframes bgMoving {
    0%{
        background-position: 5% 100%;
    }
    7%{
        background-position: 54% 100%;
    }
    14%{
        background-position: 52% 100%;
    }
    21%{
        background-position: 55% 100%;
    }
    28%{
        background-position: 56% 100%;
    }
    35%{
        background-position: 80% 100%;
    }
    42%{
        background-position: 100% 100%;
    }
    49%{
        background-position: 80% 100%;
    }
    56%{
        background-position: 56% 100%;
    }
    63%{
        background-position: 55% 100%;
    }
    70%{
        background-position: 52% 100%;
    }
    77%{
        background-position: 54% 100%;
    }
    84%{
        background-position: 5% 100%;
    }
    91%{
        background-position: 0% 100%;
    }
    100%{
        background-position: 5% 100%;
    }
}
